* {
  box-sizing: border-box;
  outline: none;
}

html {
  scroll-behavior: smooth;
  height: 100%;
}

body {
  margin: 0 auto;
  padding: 0;
  font-family: $main-font;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 10px;
}